import "bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import "boxicons/css/boxicons.min.css";
import "datatables.net";
import DataTable from "datatables.net-bs5";
import "datatables.net-bs5/css/dataTables.bootstrap5.min.css";
import "datatables.net-responsive";
import "datatables.net-responsive-bs5";
import "datatables.net-responsive-bs5/css/responsive.bootstrap5.min.css";
import * as echarts from "echarts";
import $ from "jquery";
import "../style/dev_dashboard.scss";

import { Modal } from "bootstrap";
import "../assets/logo only.svg";

$(function () {
  const table = new DataTable("#example", {
    pageLength: 5,
    lengthMenu: [5, 10, 15],
    responsive: true,
  });

  new DataTable("#example1", {
    pageLength: 5,
    lengthMenu: [5, 10, 15],
    responsive: true,
  });

  const companyModal = new Modal("#companyManageModal");

  $("#example tbody").on("click", "tr", function () {
    console.log("clicked: " + table.row(this).data());
    const data = table.row(this).data();
    document.getElementById("_package").value = data[3];
    document.getElementById("_name").value = data[0];
    document.getElementById("_email").value = data[1];
    document.getElementById("_address").value = data[2];

    companyModal.show();
  });

  let myChart = echarts.init(document.getElementById("main"));

  // Draw the chart
  myChart.setOption({
    title: {
      text: "Maybe some piechart of activated vs no or token related chart",
    },
    tooltip: {},
    xAxis: {
      data: ["shirt", "cardigan", "chiffon", "pants", "heels", "socks"],
    },
    yAxis: {},
    series: [
      {
        name: "sales",
        type: "bar",
        data: [5, 20, 36, 10, 10, 20],
      },
    ],
  });

  $(window).on("resize", function () {
    myChart.resize();
  });
});
